import React, { Component } from 'react'
import { Paper, Grid, IconButton } from '@material-ui/core'
import { connect } from 'react-redux'
import Connection from '../components/connection'
import { DataConsumer } from '../LocalDB'
import { Icon } from 'semantic-ui-react'
import DbSync from '../components/DbSync'

class TheFooter extends Component {
  constructor(props) {
    super(props)
    this.state = {}
  }
  
  render() {
    const { change, isSync } = this.props.sync
    const { dataload } = this.props.data
    const DBStatus = () => {
      return (
        <DataConsumer>
          {({ loadAllData }) => (
            <>
              <IconButton
                color={dataload ? "primary" : "secondary"}
                size="small"
              >
                <Icon name='database' size='small' />
              </IconButton>
              <IconButton
                size="small"
                onClick={() => loadAllData()}
              >
                {dataload ? <h6 style={{ fontSize: '10px', color: 'black', margin: 0 }}>Database Connected</h6> :
                  <h6 style={{ fontSize: '10px', color: 'black', margin: 0 }}>Database Disconnected </h6>}
              </IconButton>
            </>
          )}
        </DataConsumer>
      )
    }
    return (
      <Paper style={{
        height: '20px',
        fontSize: '10px',
        borderRadius: 0,
        position: 'static'
      }}>
        <Grid container>
          <Grid item xs={'auto'} sm={'auto'}>
            {DBStatus()}
          </Grid>
          <Grid item xs={4} sm={4}>
            <React.Fragment>
            <DbSync
              change={change}
              isSync={isSync}
            />
            </React.Fragment>
          </Grid>
          <Grid item xs={4} sm={4}>
            <Connection />
          </Grid>
        </Grid>
      </Paper>
    )
  }

}
const mapStateToProps = (state) => {
  return {
    sync: state.SyncData,
    data: state.DataStore,
  }
}

export default React.memo(connect(mapStateToProps)(TheFooter))
