import React, { useContext, useState, useEffect } from 'react';
import Controls from '../../../../components/controls/Controls'
import { Grid } from '@material-ui/core';
import { connect } from 'react-redux'
import { BillingDetails } from '../../../../store/action/Cart'
import { ShopHandeler } from '../../../../LocalDB/ShopDB'
const Saletype = [
    { _id: 'immediateSale', name: 'Immediate Sale' },
    { _id: 'booking', name: 'Booking' }
]

const General = (props) => {
    const { users, loadAllusers } = useContext(ShopHandeler)

    const initialFValues = {
        Mobile: '',
        Name: '',
        SaleType: 'immediateSale',
        orderBy: '',
        free: false,
        // discount: false,
    }
    const [values, setValues] = useState(initialFValues);
    const handleInputChange = e => {
        const { name, value } = e.target
        setValues({
            ...values,
            [name]: value
        })
        props.BillingDetails(name, value)
    }
    useEffect(() => {
        loadAllusers()
    }, [loadAllusers])

    return (
        <Grid container spacing={1}>
            <Grid item xs={6} sm={6} >
                <Grid container spacing={1}>
                    <Grid item xs={12} sm={12} >
                        <Controls.Input
                            name='Mobile'
                            label='Customer Mobile'
                            type='number'
                            value={values.Mobile}
                            size="small"
                            fullWidth
                            onChange={handleInputChange}
                        // error={Mobile && Mobile.length < 9 ? "Minimum 10 numbers required." : ""}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} >
                        <Controls.Input
                            name='Name'
                            label='Customer Name'
                            type='text'
                            size="small"
                            fullWidth
                            value={values.Name}
                            onChange={handleInputChange}
                        />
                    </Grid>
                    <Grid item xs={12} sm={12} >
                        <Controls.Select
                            label="Order Take By"
                            name="orderBy"
                            size="small"
                            fullWidth
                            options={users}
                            optionsValue={'username'}
                            optionsDisplay={'EmpolyeName'}
                            value={values.orderBy}
                            onChange={handleInputChange}
                        />

                    </Grid>
                </Grid>
            </Grid>
            <Grid item xs={6} sm={6} >
                <Controls.RadioGroup
                    name="SaleType"
                    value={values.SaleType}
                    onChange={handleInputChange}
                    options={Saletype}
                    size="small"
                    optionsValue={'_id'}
                    optionsDisplay={'name'}
                />
                <Controls.Checkbox
                    name="free"
                    label='Complementary'
                    size="small"
                    value={values.free}
                    onChange={handleInputChange}
                />
            </Grid>
        </Grid>
    )
}

const mapStateToProps = (state) => {
    return {
        data: state.DataStore,
        Cart: state.Cart,
    }
}
export default connect(mapStateToProps, { BillingDetails })(General);