import React, {  useEffect, useContext } from "react";
import { IconButton } from '@material-ui/core'
import { DataConsumer } from '../LocalDB'
import { Icon } from 'semantic-ui-react'

function DbSync(props) {

    const { syncData } = useContext(DataConsumer)
    const { change, isSync } = props

    useEffect(() => {
        console.log(change)
        if(change > 0){
            if (isSync === false) {
                syncData()
                .then((d) => {
                    console.log(d)
                })
                .catch((err) => {
                    console.log(err)
                });
            }
        }
    }, [change, syncData , isSync]);

    const lastSync = () => {
        const currentdate = new Date();
        const datetime = "Last Sync: " + currentdate.getDate() + "/"
            + (currentdate.getMonth() + 1) + "/"
            + currentdate.getFullYear() + " @ "
            + currentdate.getHours() + ":"
            + currentdate.getMinutes() + ":"
            + currentdate.getSeconds();
        return datetime
    }

    return (
        <>
            <IconButton
                color={change !== 0 ? "secondary" : "primary"}
                size="small"
            >
                <Icon loading={isSync} name='sync' size='small' />
                {change !== 0 && <h6 style={{ fontSize: '10px', margin: 0 }}>{change}</h6>}
            </IconButton>
            <IconButton
                size="small"
                onClick={() => {
                    syncData()
                        .then((d) => {
                            console.log(d)
                        })
                        .catch((err) => {
                            console.log(err)
                        });
                }}
            >
                {change !== 0 ? <h6 style={{ fontSize: '10px', color: 'black', margin: 0 }}>{'Sync now'}</h6> :
                    <>
                        <h6 style={{ fontSize: '10px', color: 'black', margin: 0 }}>{lastSync()}</h6>
                    </>
                }
            </IconButton>
        </>
    );
}

export default DbSync;